.lightbox-on,
.lightbox-lock > body {
	height: 100%;
	overflow: hidden;
	touch-action: none;
}

/*
 * 1: Average browser max z-index.
 * 2: IE8 transparent background.
 * 3. Use the iOS devices hardware accelerator to provide native scrolling.
 * 4. Prevent font rendering issues on Webkit.
 */
.lightbox-overlay {
	-webkit-backface-visibility: hidden; /* 4 */
	background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVQI12NgYGDYDAAAuAC0TCbBxgAAAABJRU5ErkJggg==');
	background-repeat: repeat;
	bottom: 0;
	height: 100%;
	left: 0;
	overflow: hidden;
	-webkit-overflow-scrolling: touch; /* 3 */
	position: fixed;
	right: 0;
	text-align: center;
	top: 0;
	width: 100%;
	z-index: 2147483647; /* 1 */

	&:before {
		content: '';
		display: inline-block;
		height: 100%;
		margin-left: -0.25em;
		margin-right: -1px;
		vertical-align: middle;
		width: 1px;
	}

	/*
	 * 1: Embedded animated gif.
	 */
	&.lightbox-loader:after {
		background-image: url('data:image/gif;base64,'); /* 1 */
		background-position: center center;
		background-repeat: no-repeat;
		border-radius: 100%;
		bottom: 0;
		content: ' ';
		height: 28px;
		left: 0;
		margin: auto;
		position: absolute;
		right: 0;
		top: 0;
		width: 28px;
		z-index: 3;
	}
}

/*
 * 1: Forces height calculations to work in JavaScript.
 */
.lightbox-header,
.lightbox-footer {
	left: 0;
	margin: -1px 0; /* 1 */
	overflow: hidden;
	padding: 1px 1em; /* 1 */
	position: fixed;
	right: 0;
	top: 0;
	width: 100%;
	z-index: 1;
}

.lightbox-header {
	background: $lightbox-header-background;
	color: $lightbox-header-color;
	padding-right: 4.555em;
}

.lightbox-footer {
	background: $lightbox-footer-background;
	bottom: 0;
	color: $lightbox-footer-color;
	top: auto;
}

.lightbox {
	display: inline-block;
	margin: 0 auto;
	max-height: 100%;
	max-width: 100%;
	overflow: auto;
	vertical-align: middle;

	&.lightbox-ajax {
		max-width: 1140px;
		width: 100%;
	}

	&.lightbox-iframe {
		max-width: 900px;
		overflow: hidden;
		width: 100%;
	}

	&.lightbox-image { overflow: hidden; }

	> img {
		max-height: 100%;
		max-width: none\9;
		width: auto\9;

		/* IE9 and IE10 still read the \9 hack*/
		&:nth-of-type(1n) { max-width: 100%\9; }
	}

	> .media {
		background-color: $lightbox-media-background-color;
		margin: 0 auto;
	}
}

.lightbox-content {
	background-color: $lightbox-content-background-color;
	max-height: 100%;
	overflow: auto;
	padding: 1em;
	text-align: left;
}

.lightbox-close {
	color: $carousel-control-color;
	display: inline-block;
	font-size: 1.5em;
	font-weight: bold;
	padding: 0.83em;
	position: fixed;
	right: 0;
	text-decoration: $carousel-control-color-hover;
	top: 0;
	z-index: 2;

	&:hover {
		color: $carousel-control-color;
		text-decoration: $carousel-control-text-decoration-hover;
	}
}

.lightbox-direction {
	&,
	&:visited {
		color: $carousel-control-color;
		display: block;
		font-size: 2em;
		font-weight: bold;
		left: 0;
		padding: 0.5em;
		position: fixed;
		text-align: center;
		text-decoration: $carousel-control-text-decoration;
		top: 49%;
		width: 2em;
		z-index: 2;
	}

	&.right {
		left: auto;
		right: 0;
	}

	&:hover {
		color: $carousel-control-color;
		text-decoration: $carousel-control-text-decoration-hover;
	}
}
