// ---------------------------------------------------------------------- //
//	General Styles
// ---------------------------------------------------------------------- //

$site-background: #fff !default;
$site-width: 1140px !default;
$site-min-width: 320px !default;

$base-font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif !default;
$base-font-size: 100% !default;
$base-font-size-px: 16px !default; // used for calculations
$base-line-height: 1.375 !default;
$base-text-color: #222 !default;

$selection-background: #b3d4fc !default;
$selection-color: #fff !default;

// ---------------------------------------------------------------------- //
//	Typography
// ---------------------------------------------------------------------- //

$paragraph-margin: null;

$heading-font-family: null;

$h1-color: null;
$h1-font-family: null;
$h1-font-size: null;
$h1-line-height: null;
$h1-text-decoration: null;
$h1-text-transform: uppercase !default;

$h2-color: null;
$h2-font-family: null;
$h2-font-size:  null;
$h2-line-height: null;
$h2-text-decoration: null;
$h2-text-transform: uppercase !default;

$h3-color: null;
$h3-font-family: null;
$h3-font-size: null;
$h3-line-height: null;
$h3-text-decoration: null;
$h3-text-transform: uppercase !default;

$h4-color: null;
$h4-font-family: null;
$h4-font-size: null;
$h4-line-height: null;
$h4-text-decoration: null;
$h4-text-transform: null;

$h5-color: null;
$h5-font-family: null;
$h5-font-size: null;
$h5-line-height: null;
$h5-text-decoration: null;
$h5-text-transform: null;

$h6-color: null;
$h6-font-family: null;
$h6-font-size: null;
$h6-line-height: null;
$h6-text-decoration: null;
$h6-text-transform: null;

// ---------------------------------------------------------------------- //
//	Grid System
// ---------------------------------------------------------------------- //

$grid-breakpoint-xs-max: 47.99em;
$grid-breakpoint-s-min: 48em;
$grid-breakpoint-s-max: 61.99em;
$grid-breakpoint-m-min: 62em;
$grid-breakpoint-m-max: 74.99em;
$grid-breakpoint-l-max: 75em;

$grid-column-count: 12 !default;
$grid-column-margin: 2% !default;

// ---------------------------------------------------------------------- //
//	Basic Elements
// ---------------------------------------------------------------------- //

/**
 * Links
 */
$link-color: #0000ee !default;
$link-text-decoration: none !default;
$link-color-hover: #000 !default;
$link-text-decoration-hover: underline !default;

/**
 * Horizontal Rule
 */
$horizontal-line-color: #c0c0c0 !default;
$horizontal-line-height: 1px !default;

/**
 * Blockquote
 */
$blockquote-background: null;
$blockquote-border-left: 5px solid #c0c0c0 !default;

/**
 * Tables
 */
$table-bordered-border: 1px solid #c0c0c0 !default;

$table-striped-background-color: #e7e7e7 !default;

// ---------------------------------------------------------------------- //
//	CSS Components
// ---------------------------------------------------------------------- //

/**
 * Forms
 */
$form-placeholder: #c0c0c0 !default;

$form-input-background: #fff !default;
$form-input-border: 1px solid #c0c0c0 !default;
$form-input-border-radius: 0 !default;
$form-input-color: #222 !default;

$form-input-background-focus: null;
$form-input-border-color-focus: #00a9ec !default;
$form-input-color-focus: null;

$form-input-background-disabled: #ebebeb !default;
$form-input-border-color-disabled: #c0c0c0 !default;
$form-input-color-disabled: #c0c0c0 !default;

$form-input-background-readonly: null;
$form-input-border-color-readonly: #ebebeb !default;
$form-input-color-readonly: null;

/**
 * Buttons
 */
$button-background: #dadada !default;
$button-border: solid 1px #c0c0c0 !default;
$button-border-radius: 0 !default;
$button-color: #222 !default;

$button-background-hover: #cdcdcd !default;
$button-border-color-hover: null;
$button-color-hover: null;

$button-background-focus: #cdcdcd !default;
$button-border-color-focus: #00a9ec !default;
$button-color-focus: null;

$button-background-active: #e7e7e7 !default;
$button-border-color-active: transparent !default;
$button-color-active: null;

$button-background-disabled: #e7e7e7 !default;
$button-border-color-disabled: #c0c0c0 !default;
$button-color-disabled: #5d5d5d !default;

/**
 * Lists
 */
$list-colored-bullet: #00a9ec !default;

/**
 * Alerts
 */
$alert-color-scheme: #f2e187 !default;

// ---------------------------------------------------------------------- //
//	JS Plugins
// ---------------------------------------------------------------------- //

/**
 * Lightbox
 */
$lightbox-header-background: transparent !default;
$lightbox-header-color: #fff !default;

$lightbox-footer-background: transparent !default;
$lightbox-footer-color: #fff !default;

$lightbox-content-background-color: #fff !default;

$lightbox-media-background-color: #111 !default;

$carousel-control-color: #fff !default;
$carousel-control-text-decoration: none !default;

$carousel-control-color-hover: #fff !default;
$carousel-control-text-decoration-hover: underline !default;

/**
 * Carousel
 */
$carousel-caption-background-color: #111 !default;
$carousel-caption-color: #fff !default;

$carousel-control-color: #fff !default;
$carousel-control-text-decoration: none !default;

$carousel-control-color-hover: #fff !default;
$carousel-control-text-decoration-hover: underline !default;

$carousel-nav-background-color: transparent !default;
$carousel-nav-border: 2px solid #111 !default;

$carousel-nav-background-color-hover: null;
$carousel-nav-border-color-hover: null;

$carousel-nav-background-color-active: #fff !default;
$carousel-nav-border-color-active: null;

/**
 * Tabs
 */
$tabs-nav-border-color: #c0c0c0 !default;

$tabs-nav-item-background-color: null;
$tabs-nav-item-border-color: transparent !default;
$tabs-nav-item-color: null;

$tabs-nav-item-background-color-hover: #ebebeb !default;
$tabs-nav-item-border-color-hover: #c0c0c0 !default;
$tabs-nav-item-color-hover: null;

$tabs-nav-item-background-color-active: #fff !default;
$tabs-nav-item-border-color-active: #c0c0c0 !default;
$tabs-nav-item-color-active: #454545 !default;

/**
 * Dropdown
 */
$accordion-border-color: #c0c0c0 !default;

$accordion-head-background-color: null;
$accordion-body-background-color: null;

$accordion-link-color: null;
$accordion-link-text-decoration: null;

$accordion-link-color-hover: null;
$accordion-link-text-decoration-hover: null;
