/*
 *  Make it responsive
 */
table {
	margin: 1em 0;
	max-width: 100%;
	width: 100%;

	th,
	td {
		padding: 0.5em;
		text-align: left;
		vertical-align: top;
	}
}

/*
 *  Bordered tables
 */
.table-bordered {
	border: $table-bordered-border;
	border-collapse: separate;
	border-left-width: 0;

	th,
	td {
		border-left: $table-bordered-border;
		border-top: $table-bordered-border;
	}

	thead:first-child tr:first-child th,
	tbody:first-child tr:first-child td { border-top-width: 0; }
}

/*
 *  Striped tables
 */
.table-striped tbody > tr:nth-child(odd) > td,
.table-striped tbody > tr:nth-child(odd) > th,
.table-row-alt > td,
.table-row-alt > th { background-color: $table-striped-background-color; }

/*
 * Overflow helpers
 */

.table-scrollable {
	margin: 1em 0;
	overflow: auto;
	overflow-y: hidden;
	width: 100%;

	table { margin: 0; }
}
